.circle {
  border-radius: 50%;
}

body {
  background: #efefef;
}

.top {
  display: flex;
  position: absolute;
  width: 100%;
  padding-top: 1.5625rem;
  z-index: 2;
  padding-bottom: 0.625rem;
}

.top > .left {
  line-height: 1.875rem;
  padding-left: 0.625rem;
}

.pot-input {
  height: 1.875rem;
  font-size: 0.75rem;
  width: 60%;
  margin-left: 0.625rem;
  border-radius: 0.9375rem;
  overflow: hidden;
  padding-left: 0.625rem;
}

.Main {
  bottom: 0;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
}

.Main .pot-scroll-wrapper {
  height: 100%;
}

.Main .pot-scroll-wrapper .pot-scroll-list-wrapper .container > .title {
  display: flex;
  padding-left: 0.625rem;
  height: 2.5rem;
  line-height: 2.5rem;
  position: relative;
}

.Main .pot-scroll-wrapper .pot-scroll-list-wrapper .container > .title .icon {
  margin-top: 0.625rem;
  width: 0.625rem;
  height: 1.25rem;
  background: #333;
  border-radius: 0.1875rem;
}

.Main .pot-scroll-wrapper .pot-scroll-list-wrapper .container > .title .left {
  margin-left: 0.625rem;
}

.Main .pot-scroll-wrapper .pot-scroll-list-wrapper .container > .title .more {
  position: absolute;
  right: 1.25rem;
}

.Main .pot-scroll-wrapper .pot-scroll-list-wrapper > .middle {
  height: 9.375rem;
  position: relative;
  background: #fff;
  margin-top: -1.25rem;
  z-index: 10;
  width: 90%;
  margin-left: 5%;
  border: 0.0625rem solid #ddd;
  border-radius: 0.625rem;
}

.Main .pot-scroll-wrapper .pot-scroll-list-wrapper > .middle .msg {
  border-top: 0.0625rem solid #ddd;
  line-height: 3.125rem;
  padding-left: 0.625rem;
  font-size: 0.75rem;
}

.Main .pot-scroll-wrapper .pot-scroll-list-wrapper > .middle .nav {
  height: 6.25rem;
  display: flex;
}

.Main .pot-scroll-wrapper .pot-scroll-list-wrapper > .middle .nav > div {
  flex: 1;
  text-align: center;
}

.Main .pot-scroll-wrapper .pot-scroll-list-wrapper > .middle .nav .icon {
  height: 2.8125rem;
  width: 2.8125rem;
  background: #333;
  color: #fff;
  border-radius: 0.625rem;
  line-height: 2.8125rem;
  margin-left: 0.625rem;
  margin-top: 0.9375rem;
  margin-bottom: 0.625rem;
}

.Main .pot-scroll-wrapper .pot-scroll-list-wrapper > .middle .nav .circle {
  margin-top: 1.875rem;
  margin-left: 0.5rem;
  border: 0.0625rem solid #ddd;
  width: 3.125rem;
  line-height: 3.125rem;
  font-size: 3.125rem;
  color: #ddd;
  height: 3.125rem;
}

.Main .pot-slide {
  width: 100%;
  height: 13.75rem;
  border-bottom: 0.0625rem solid #ddd;
}

.Main .pot-slide img {
  width: 100%;
  height: 100%;
}

.Main .pot-slide-dots {
  height: 1.875rem;
}

.Main .pot-slide-dots span {
  height: 0.3125rem;
  border-radius: 0.15625rem;
  width: 0.625rem;
  transition: 0.3s all;
  margin-right: 0.625rem;
  display: inline-block;
}

.Main .pot-slide-dots span.active {
  width: 3.125rem;
  background: #fff;
}